import React, { Component } from "react";
import DOMPurify from "dompurify";
import PlayersDataViz from "./components/PlayersDataViz/PlayersDataViz";
import Distributions from "./components/Distributions/OverlappedDistributions";

import competitionsList from "./components/competitions.json";

import CompetitionsComparison from "./components/CompetitionsComparison/CompetitionsComparison";
import TeamsComparison from "./components/TeamsComparison/TeamsComparison";
import OneClubPlayers from "./components/PlayersComparison/OneClubPlayers";
import Journeymen from "./components/PlayersComparison/Journeymen";
import "./App.css";

import translations from "./translations.json";

const PLAYERS_THRESHOLD = 3750;

window.getImage = () => {
  const img = document.createElement("img");
  const canvas = document.getElementsByTagName("canvas")[0];
  img.src = canvas.toDataURL();
  document.body.appendChild(img);
};

class App extends Component {
  state = {
    lang: "en"
  };
  componentDidMount() {
    this.setState({
      lang: document.querySelector("html").getAttribute("lang")
    });
  }
  sendAnalytics(player, event = "search-player") {
    if (!player) {
      return;
    }
    // console.log('sendAnalytics',event,player)
    if (typeof window.gtag !== "undefined") {
      window.gtag("event", event, {
        event_category: "engagement",
        event_label: player
      });
    }
  }
  render() {
    const { lang } = this.state;
    return (
      <div className="content-wrapper essay">
        <div className="container-fluid">
          {/*<Header />*/}
          <div className="row no-margin-row">
            <div className="col-xs-12">
              <section>
                <PlayersDataViz
                  file="prefetchedPlayers.json"
                  lang={lang}
                  analyticsCallback={player => this.sendAnalytics(player)}
                />
              </section>
            </div>
          </div>
          <div className="row">
            <div className="col-xs-12 col-sm-6 col-sm-offset-3">
              <section>
                <h2>I campionati</h2>
                <p>
                  &Egrave; la Bundesliga tedesca il campionato in cui un
                  giocatore passa pi&ugrave; tempo nello stesso club, quasi tre
                  anni. Pi&ugrave; in basso la Serie A con periodi di permanenza
                  pi&ugrave; brevi, tuttavia in serie A hanno giocato bandiere,
                  come Paolo Maldini, Francesco Totti o Alessandro Costacurta.
                </p>
                <CompetitionsComparison
                  competitionsList={competitionsList}
                  playersThreshold={PLAYERS_THRESHOLD}
                  lang={lang}
                />
              </section>
              <section>
                {lang === "it" && <h2>Le squadre</h2>}
                {lang === "it" && (
                  <p>
                    Non tutti i giocatori diventano bandiere della squadra in
                    cui giocano, casi come quelli di Totti o Maldini sono rari,
                    l'analisi approfondita delle carriere ci racconta una storia
                    diversa.
                    <br />
                    Ci sono squadre per cui il senso di attaccamento &egrave;
                    maggiore e i giocatori tendono a rimanervi per pi&ugrave;
                    anni.
                    <br />
                    Athletic Bilbao e Real Sociedad, grazie alla regola per cui
                    solamente giocatori baschi possono giocare nelle loro fila
                    (per la Real Sociedad solo fino al 1989) e il grande
                    orgoglio nazionalista, registrano il numero anni di
                    permanenza medio pi&ugrave; alto.
                  </p>
                )}
                {lang === "en" && <h2>Teams loyalty</h2>}
                {lang === "en" && (
                  <p>
                    Football is changing fast. Famous and reach teams are
                    getting bigger, smaller sides are struggling to keep their
                    young players dazzled by unreachable wages and dreams of
                    glory.
                    <br />
                    Loyalty has become a We see big teams getting even bigger
                    and meaner. Smaller sides doing the best they can to punch
                    above their weight. The entire sport is now more corporate
                    than ever and loyalty has turned into an alien concept.{" "}
                  </p>
                )}
                <TeamsComparison
                  competitionsList={competitionsList}
                  playersThreshold={PLAYERS_THRESHOLD}
                  lang={lang}
                />
              </section>
              <section>
                <h2>{translations[lang].flags.h2}</h2>
                <p
                  dangerouslySetInnerHTML={{
                    __html: DOMPurify.sanitize(translations[lang].flags.p)
                  }}
                />
                <OneClubPlayers
                  competitionsList={competitionsList}
                  playersThreshold={PLAYERS_THRESHOLD}
                  lang={lang}
                />
              </section>

              <section>
                <h2>{translations[lang].journeymen.h2}</h2>
                <p
                  dangerouslySetInnerHTML={{
                    __html: DOMPurify.sanitize(translations[lang].journeymen.p)
                  }}
                />
                <Journeymen lang={lang} />
              </section>
              <section>
                {lang === "it" && <h2>Gli effetti della legge Bosman</h2>}
                {lang === "en" && <h2>The Bosman Ruling</h2>}
                {lang === "it" && (
                  <p>
                    La <i>sentenza Bosman</i> ha aperto i confini del calcio nel
                    1995. La vita di tanti giocatori &egrave; cambiata e per la
                    grande maggioranza &egrave; aumentato il numero di maglie
                    vestite durante il corso di una carriera
                    <br />
                    Questo grafico mostra come, in seguito alla sentenza nel
                    1995, la tendenza di giocare per pi&ugrave; squadre sia
                    aumentata (
                    <i>
                      nel grafico vengono inclusi solamente giocatori con almeno
                      10 anni di esperienza
                    </i>
                    ).
                  </p>
                )}
                {lang === "en" && (
                  <p>
                    The <i>Bosman ruling</i> removed a lot of the limitations on
                    the free transfer of players in Europe.
                    <br />
                    Football changed, for many players the number of teams they
                    played for and countries they visited increased. The
                    following chart shows how playing for more teams has become
                    a common event in the life of a football player after 1995 (
                    <i>
                      including only players with at least ten years career's
                      length
                    </i>
                    ).
                  </p>
                )}
                <Distributions
                  file="distributions.json"
                  lang={lang}
                  max={0.2}
                  groupMap={
                    lang === "it"
                      ? {
                          before: {
                            title: "Prima della sentenza",
                            subtitle: "1930-1995"
                          },
                          after: {
                            title: "Dopo la sentenza",
                            subtitle: "1995-2018"
                          }
                        }
                      : {
                          before: {
                            title: "Before the ruling",
                            subtitle: "1930-1995"
                          },
                          after: {
                            title: "After the ruling",
                            subtitle: "1995-2018"
                          }
                        }
                  }
                />
              </section>
              <section>
                <h2>Data</h2>
                <h4>Stranezze nei dati / Anomalies in the data</h4>
                {/*<p>
                https://www.nytimes.com/interactive/2017/12/29/upshot/internationalization-of-pro-sports-leagues-premier-league.html#wfn-italy-serie-a
                <br/>
                https://statathlon.com/the-impact-of-foreign-players-on-the-performance/
                <br/>
                https://www.researchgate.net/publication/227574479_The_Effects_of_the_Bosman_Ruling_on_National_and_Club_Teams_in_Europe
                </p>*/}
              </section>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default App;



